
import Vue from 'vue'

export default Vue.extend({
  name: 'SettingsSelect',
  props: {
    items: null as any,
    label: {
      type: String,
    },
    value: {
      type: [String, Number],
    },
    display: {
      type: String,
    },
  },
  data () {
    return {
      input: '',
    }
  },
  watch: {
    value: {
      handler (after) {
        this.input = after
      },
      immediate: true,
    },
  },
  methods: {
    updateInput () {
      this.$emit('input', this.input)
    },
  },
})
