
import Vue from 'vue'

export default Vue.extend({
  name: 'ShortcutHelpDialog',
  data: () => {
    return {
      model: false,
    }
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    shortcuts: {
      type: Object,
      required: true,
    },
  },
  watch: {
    model (val) {
      this.$emit('input', val)
    },
    value (val) {
      this.model = val
    },
  },
})
