
import Vue from 'vue'

export default Vue.extend({
  name: 'SettingsSwitch',
  props: {
    label: {
      type: String,
    },
    value: {
      type: Boolean,
    },
    status: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      input: '',
    }
  },
  watch: {
    value: {
      handler (after) {
        this.input = after
      },
      immediate: true,
    },
  },
  methods: {
    updateInput () {
      this.$emit('input', this.input)
    },
  },
})
